import { FC } from 'react';

import JumpLink from '#components/JumpLink';
import ProductTitle from '#components/ProductTitle';
import ProductViewLayout from '#components/ProductViewLayout';
import SpecsList from '#components/SpecsList/SpecsList';
import StarRating from '#components/StarRating';
import TextBlock from '#components/TextBlock';

import {
  StyledLeftPanel,
  StyledImage,
  StyledFooter,
} from './ProductPanel.styles';
import { ProductPanelProps } from './ProductPanel.types';

const ProductPanel: FC<ProductPanelProps> = ({
  title,
  rating,
  image,
  items,
  description,
  jumpLink,
}) => (
  <ProductViewLayout
    top={
      <>
        {title && <ProductTitle title={title} />}
        {rating && <StarRating value={rating} />}
      </>
    }
    left={
      <StyledLeftPanel>
        {image && <StyledImage>{image}</StyledImage>}
        {items && <SpecsList items={items} />}
      </StyledLeftPanel>
    }
    right={
      <>
        <TextBlock>{description}</TextBlock>
        <StyledFooter>Price Comparison</StyledFooter>
        {jumpLink && <JumpLink {...jumpLink} />}
      </>
    }
  />
);

export default ProductPanel;
